let _self
import {uniFab} from '@dcloudio/uni-ui'
import {flixedadd} from '../components/dragButton/xiaosikaifa-flixedadd.vue'
export default {
	data() {
		return {
			scrollLeft: 0,
			isClickChange: false,
			currentTab: 0,
			menuTabs: [{
				name: '热点'
			}, {
				name: '考研'
			}, {
				name: '就业'
			}, {
				name: '专业'
			}, {
				name: '我的'
			}],
			swiperDateList: [[],[],[],[],[]],
			addlistdata:[
				{
					title:"发起话题",
					src:"/static/img/01.JPG"
				},
				{
					title:"第二个",
					src:"/static/img/02.JPG"
				}
			],
		}
	},
	components:{
		uniFab,
		flixedadd
	},
	onLoad: function() {
		_self = this
		//初始化数据
	/* 	console.log(',') */
		for(let i =0;i < _self.swiperDateList.length;i++){
			_self.getDateList(i);
		}	
	},
	onNavigationBarButtonTap() {
		uni.navigateTo({
			url:'../mine/mine'
		})
	},
	methods: {
		swichMenu: async function(current) { //点击其中一个 menu
				if (this.currentTab == current) {
					return false;
				} else {
					this.currentTab = current;
					this.setScrollLeft(current);
				}
			},
		swiperChange: async function(e) {
					let index = e.target.current;
					this.setScrollLeft(index);
					this.currentTab = index; 
				},
		setScrollLeft: async function(tabIndex) {
						let leftWidthSum = 0;
						for (var i = 0; i <= tabIndex; i++) {
							let nowElement = await this.getWidth('tabNum' + i);
							leftWidthSum = leftWidthSum + nowElement.width;
						}
						let winWidth = uni.getSystemInfoSync().windowWidth;
						this.scrollLeft = leftWidthSum > winWidth ? (leftWidthSum - winWidth) : 0
					},
		getWidth: function(id) { //得到元素的宽高
						return new Promise((res, rej) => {
							uni.createSelectorQuery().select("#" + id).fields({
								size: true,
								scrollOffset: true
							}, (data) => {
								res(data);
							}).exec();
						})
					},
		loadMore: function(tabIndex) {
						console.log('正在加载更多数据。。。')
						this.getDateList(tabIndex);
					},
		getDateList: function(tabIndex) {
						uni.request({
							url:'http://47.97.213.36:7300/mock/5e145ffaf2ecc141ee032ab8/leader/newsList',
							method:'GET',
							success:function(res){
								/* console.log(res.data.data) */
								_self.swiperDateList[tabIndex] = res.data.data[tabIndex]
							}
						})
					},
		newsDetail:function(type,listIndex){
					console.log(type + ',' + listIndex)
					uni.navigateTo({
						url:'../content/newsDetail?type=' + type + '&listIndex=' + listIndex
					})
				},
		addlisttap:function(index){/*第一个序列为0,第二个为1,以此类推;当值为-1代表点击的是遮慕层*/
			if(index === 0){
				uni.navigateTo({
					url:'../content/newTopic'
				})
			}
		}
	}
}
